<template>
<div class="app-page auto-distribution-set">
    <page-content class="page-content">
      <div class="distribution-sel distribution-date">
        <div class="sel-title vertical-center"><span>配送日期</span></div>
        <div class="sel-list">
          <div class="sel-item vertical-center" v-for="(item, index) in distributionDate.list" :key="index">
            <div class="item-inner vertical-center" @click="clickItem(distributionDate, item, index)">
              <div class="item-img">
                <img :src="item.isSelect?'/static/images/commonicon/xz1.png':'/static/images/commonicon/wxz1.png'"/>
              </div>
              <div class="item-label">{{item.label}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="distribution-sel distribution-deadline">
        <div class="sel-title vertical-center"><span>配送期限</span></div>
        <div class="sel-list">
          <div class="sel-item vertical-center" v-for="(item, index) in distributionDeadline.list" :key="index">
            <div class="item-inner vertical-center" @click="clickItem(distributionDeadline, item, index)">
              <div class="item-img">
                <img :src="item.isSelect?'/static/images/commonicon/xz1.png':'/static/images/commonicon/wxz1.png'"/>
              </div>
              <div class="item-label">{{item.label}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="distribution-confirm flex-center">
        <van-button >确认</van-button>
      </div>
    </page-content>
</div>

</template>
<script>
import PageContent from '@/components/page/content'
export default {
  name: 'autoDistributionSet',
  components: { PageContent },
  data () {
    return {
      // 配送日期
      distributionDate: {
        selectOne: {},
        list: [{ id: '1', label: '星期一', isSelect: true }, { id: '2', label: '星期二', isSelect: false }, { id: '3', label: '星期三', isSelect: false },
          { id: '4', label: '星期四', isSelect: false }, { id: '5', label: '星期五', isSelect: false }, { id: '6', label: '星期六', isSelect: false },
          { id: '7', label: '星期天', isSelect: false }]
      },
      // 配送期限
      distributionDeadline: {
        selectOne: {},
        list: [{ id: '1', label: '一个月', isSelect: true }, { id: '2', label: '两个月', isSelect: false }, { id: '3', label: '三个月', isSelect: false },
          { id: '4', label: '六个月', isSelect: false }, { id: '5', label: '九个月', isSelect: false }, { id: '6', label: '一年', isSelect: false },
          { id: '7', label: '两年', isSelect: false }]
      }
    }
  },
  methods: {
    // 点击某个选项触发，日期/期限 公用
    clickItem (data, curItem, index) {
      if (!curItem.isSelect) {
        data.list = data.list.map(item => {
          item.isSelect = false
          return item
        })
        curItem.isSelect = true
      }
      data.selectOne = curItem
    }
  },
  created () {
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .auto-distribution-set{
    font-family: $font-family-Medium!important;
    .page-content{
      background-color: #f9f9f9;
      .distribution-sel+.distribution-sel{
        margin-top: 60px;
        background-color:#fff;
      }
      .distribution-sel{
        width: 100%;
        background-color:#fff;
        .sel-title{
          width: 100%;height: 88px;
          /*border-bottom: 1px solid $whitee6e6e6;*/
          font-size: 30px;
          color: $black333333;
          span{
            margin-left: 20px;
          }
        }
        .sel-list{
          width: 100%;
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          .sel-item{
            width: 50%;height: 88px;
            .item-inner{
              .item-img{
                width: 40px;height: 40px;
                margin-left: 54px;
                img{
                  width: 100%;height: 100%;
                }
              }
              .item-label{
                margin-left: 20px;
                font-size: 30px;
              }
            }
          }
        }
      }
      .distribution-confirm{
        width: 100%;height: 76px;
        position: absolute;
        bottom: 60px;
        .van-button{
          width: 95%;height: 100%;
          border-radius: 10px;
          background-color: $green22ac38;
          color: $whiteffffff;
          font-family: $font-family-default!important;
          letter-spacing: 10px;
        }
      }
    }
  }
</style>



